import ReactPlayer from "react-player"
import { Circles  } from 'react-loading-icons' 
import SelectSubtitle from '../../Components/SelectSubtitle/SelectSubtitle'
import { useEffect, useState } from 'react'



const PlayerMovie = ({movieUrl, subUrl,subtitles, reloadPlayer, playerReference, useGlobalSocket, setPlaying, playing, setSubUrl, roomObj}) => {

    const [selectedSub, setSelectedSub] = useState({langcode:''})

    useEffect(() => {
        console.log(selectedSub)
    }, [selectedSub])
    return (
        <>
            {!movieUrl ?
                <Circles  stroke="#000000"/>
            :
                <ReactPlayer 
                    url={movieUrl} 
                    width='75%'
                    crossOrigin="use-credentials"
                    height='100%'
                    controls={true}
                    playing={playing}
                    preload='false'
                    ref={playerReference}
                    playsinline={true}
                    onPlay={
                        () => {
                            if(useGlobalSocket){
                                if(roomObj){
                                    setPlaying(true)
                                    useGlobalSocket.emit("playSend", (playerReference.current.getCurrentTime()))
                                }

                            }
                        }
                    }
                    onPause={
                        () => {
                            setPlaying(false)
                            if(useGlobalSocket){
                                useGlobalSocket.emit("pause")
                            }
                        }
                    }
                    config={{
                        file: {
                            attributes: {
                                controlsList: 'nodownload',
                                crossOrigin: 'true',
                                style: {
                                    width: '100%',
                                    height: '100%',
                                    outline: 'none'
                                },
                                id: 'subFile'
                            },
                            
                            tracks: [
                                {
                                    kind: 'subtitles',
                                    src: subUrl,
                                    srcLang: selectedSub.langcode,
                                    default: true,
                                },

                            ]
                        }
                    }}
                />
            }
            {
                subtitles && movieUrl &&
                <SelectSubtitle setSubUrl={setSubUrl} reloadPlayer={reloadPlayer} subUrl={subUrl} subtitles={subtitles} setSelectedSub={setSelectedSub}/>
            
            }
        </>
    
    )
}
export default PlayerMovie