import styled from "styled-components"

export const Container = styled.div`
    height: 73vh;
    width: 25%;
    background: #2f323b;
`

export const ChatWindow = styled.div`
    flex: auto;
    padding: 20px;
    ${`min-height: calc(100% - 30px)`};
    ${`max-height: calc(100% - 30px)`};
    background: #2f323b;
    overflow: auto;
`

export const Messagens = styled.div`

    display: flex;
`


export const MessageContainer = styled.div`
    position: relative;
    display: inline-block;
    width: 100%;
    margin: 0 0 10px 0;
    padding: 0;

`
export const MessageBox = styled.div`
    display: flex;
    flex-direction: column;
    background:${props => props.myMsg ? '#2671ff' : '#5b5e6c'};    
    padding: 10px 10px 0 10px;
    border-radius: 0 6px 6px 0;
    max-width: 80%;
    width: auto;
    float: ${props => props.myMsg ? 'right' : 'left'};
    box-shadow: 0 0 2px rgb(0 0 0 / 12%), 0 2px 4px rgb(0 0 0 / 24%);
`

export const Message = styled.p`
    margin-top:8px;
    display: inline-block;
    font-size: 11pt;
    line-height: 13pt;
    margin: 0 0 4px 0;
`


export const MessageInput = styled.input`
    height: 30px;
    padding: 0;
    width: 99%;
    background: #40434e;
    color: white;
    outline: none;
    text-indent: 10px;
`

export const Timestamp = styled.span`
    color: rgba(0,0,0,.38);
    font-size: 8pt;
    margin-bottom: 10px;
`

export const SmallSizeSpan = styled.span`
    color: rgba(0,0,0,.38);
    font-size: 8pt;
`
