import { useEffect, useState } from 'react'
import {ButtonDiv, CloseSub, ConfigButton, ConfigContainer, Container, OffsetText, OpenSub, Subdiv, SubLanguage} from './styles'


const SelectSubtitle = ({subtitles, reloadPlayer, subUrl, setSubUrl, setSelectedSub}) => {
    
    const electron = window.require('electron');

    const ipcRenderer = electron.ipcRenderer;

    const [subOffsetValue, setSubOffsetValue] = useState(0)
    const [Open, setOpen] = useState(true)


    const offSetSub = (offSetValue) => {
        const subFile = document.getElementById('subFile');
        setSubOffsetValue(subOffsetValue + offSetValue)
        if (subFile) {
            Array.from(subFile.textTracks).forEach((track) => {
                if (track.mode === 'showing') {
                    Array.from(track.cues).forEach((cue) => {
                        cue.startTime += offSetValue;
                        cue.endTime += offSetValue;
                    });
                    return true;
                }
            });
        }
    }

    const selectSub = (subObj) => {
        ipcRenderer.invoke('selectSubtitle', subObj)
        setSelectedSub(subObj);
        reloadPlayer()
    }

    
    return (
        <>
        {
            
            Open?
            <>

                <Container>
                <CloseSub onClick={() => setOpen(false)}>X</CloseSub>

                    {
                            !subUrl?
                            Object.keys(subtitles).map((key) => (
                                <Subdiv>
                                    <SubLanguage  onClick={() => selectSub(subtitles[key])}>{subtitles[key].lang}</SubLanguage>
                                </Subdiv>
                            ))
                        :
                        <>
                            <ConfigContainer>
                                <ButtonDiv>
                                    <ConfigButton onClick={() => setSubUrl(false)}>Show sub</ConfigButton>

                                </ButtonDiv>
                                <ButtonDiv>
                                    <ConfigButton onClick={() => offSetSub(0.5)}>Adiantar</ConfigButton>

                                </ButtonDiv>
                                <ButtonDiv>

                                    <ConfigButton onClick={() => offSetSub(-0.5)}>Atrasar</ConfigButton>
                                </ButtonDiv>
                                <ButtonDiv>


                                    <ConfigButton onClick={() => reloadPlayer()}>Reload</ConfigButton>
                                </ButtonDiv>

                            </ConfigContainer>
                            <OffsetText>offSet {subOffsetValue}</OffsetText>
                        </>
                    }
                </Container>
            </>
            : 
            <OpenSub onClick={() => setOpen(true)}/>
        }
        </>
 
    )
}

export default SelectSubtitle