import { useEffect, useState } from 'react'
import socketIOClient from "socket.io-client";
import HeadBar from '../../Components/HeadBar/headBar';
import { Container, RoomContainer, RoomMovieImage, RoomMovieName, AlignImageAndDetailsRoom, RoomName, UserCont, AlignDetailsColum, Entrar} from './styles';
import { useHistory } from "react-router-dom";
import { goToLocaleProps } from '../../Functions/goTo'



const RoomsList = () => {

    const [rooms, setRooms] = useState([])
    const [userName, setUserName] = useState('')
    const globalSocketEndPoint = "https://socket-stremio-6h5gx42wjq-uc.a.run.app";
    const [useGlobalSocket, setUseGlobalSocket] = useState(null)
    const history = useHistory();

    useEffect(() => {
        const socket = socketIOClient(globalSocketEndPoint);

        setUseGlobalSocket(socket)

        socket.emit('getAllRooms')

    }, [])

    useEffect(() => {
        if(useGlobalSocket){

            useGlobalSocket.on('setRooms', (rooms) => {
                setRooms(rooms)
            })
        }
    }, [useGlobalSocket])

    useEffect(() => {
        console.log(rooms)
    }, [rooms])

    const connectRoom = (room) => {
        if(userName != ''){
            goToLocaleProps('player', {'movie': room.movie, 'torrent': room.selectTorrent, 'directConnect':userName, 'room':room }, history)

        }
    }

    return (
        <>
            <HeadBar/>            
            <Container>
                <input value={userName} onChange={(evt) => setUserName(evt.target.value)} placeholder="UserName"/>
                {
                    rooms.map((room) => (
                        <RoomContainer>
                            <RoomMovieName>{room.movie.title}</RoomMovieName>
                            <AlignImageAndDetailsRoom>
                                <RoomMovieImage url={room.movie.medium_cover_image}/>
                                <AlignDetailsColum>
                                    <RoomName>{room.name}</RoomName>
                                    <UserCont>{room.users.length} User</UserCont>
                                    <Entrar onClick={() => connectRoom(room)}>Entrar</Entrar>
                                </AlignDetailsColum>

                            </AlignImageAndDetailsRoom>

                        </RoomContainer>
                    ))
                }

    
            </Container>
        </>

    
    )
}
export default RoomsList