import styled from "styled-components"
import iconEngranagem from '../../Icons/EngrenagemIcon.png'

export const Container = styled.div`
    overflow-y: auto;
    position: absolute;
    background: #2f323b;
    display:flex;
    flex-direction: column;
    max-height: 10rem;
    right: 25%;
    padding: 20px;
`

export const Subdiv = styled.div`


`

export const SubLanguage = styled.h3`
    background: black;
    border-radius: 12px;
    padding:5px;
    margin-block:.3rem;
    :hover{
        cursor: pointer;
        background-color:#393D47;
    }
`


export const ConfigContainer = styled.div`
    display: flex;
    margin-bottom: 1rem;
`

export const ButtonDiv = styled.div`
    margin: 10px 10px 0px 10px;
`

export const ConfigButton = styled.button`
    border-radius:60;
    box-shadow: none;
    background-color:purple;
    color: white;
    :hover{
        cursor: pointer;
    }
`

export const OffsetText = styled.h3`
    align-self: center;
`

export const CloseSub = styled.p`
    position: absolute;
    top:0;
    right:10px;
    :hover{
        cursor: pointer;
    }
`

export const OpenSub = styled.div`
    position: absolute;
    opacity: 0.1;
    right:25%;
    width: 64px;
    height: 64px;
    background-image:url(${iconEngranagem});
    :hover{
        cursor: pointer;
        opacity: 1;
    }
`