import {Cartaz, Container, Descreption, Title} from './styles'
import {goToLocaleProps} from '../../Functions/goTo'
import { useHistory } from 'react-router';

export const MovieCartaz = (data) => {
    const history = useHistory();

    const movie = data.movie
    return(

        <Cartaz url={movie.large_cover_image} onClick={() => goToLocaleProps('selectTorrent', movie, history)}>
            <Title>{movie.title}</Title>
        </Cartaz>  

    )
}

export default MovieCartaz