import { BG, Carrousel, CarrouselFace, Container, GeneryTitle, LoadingIcon, MovieTitlle } from './styles'
import React, { useEffect, useState } from 'react'
import './carrousel.css'
import axios from 'axios'
import { useHistory } from "react-router-dom";
import {goToLocaleProps} from '../../Functions/goTo'

const ListRotate = ({genery, filterSortBy}) => {
    const [moviesList, setMoviesList] = useState(null)
    const history = useHistory();
    const [loading, setLoading] = useState(true)

    useEffect(() => {

        if(genery){

            axios.get(`https://yts.mx/api/v2/list_movies.json?genre=${genery}&with_rt_ratings=true&sort_by=${filterSortBy}`)
                .then((response) => {
                    setMoviesList(response.data.data.movies)
                    setLoading(false)
                })
                .catch((error) => {
                    console.error(error);
                })

        }
    }, [genery, filterSortBy])

    return(
        <Container>
            <GeneryTitle>{genery}</GeneryTitle>
            {
                loading&&
                <LoadingIcon></LoadingIcon>
            }
            <Carrousel>{

                moviesList &&

                    moviesList.slice(0,9).map((item) => (  
                            <CarrouselFace  key={item.id} onClick={() => goToLocaleProps('selectTorrent', item, history)}>
                                <BG url={item.large_cover_image}/>
                                <MovieTitlle>{item.title}</MovieTitlle>
                                <p>{item.torrents[0].quality} : {item.torrents[0].seeds}</p>

                            </CarrouselFace>

                    ))
            }  
            </Carrousel>
            
    </Container>

    )
}

export default ListRotate