import HeadBar from '../../Components/HeadBar/headBar';
import vtt from 'vtt-live-edit'
import socketIOClient from "socket.io-client";
import PlayerMovie from './PlayerMovie'
import Chat from '../../Components/Chat/Chat'
import { useCallback, useEffect, useRef, useState } from 'react';
import {Container, MovieTitle, TopDiv, CenterDiv, PlayerAndChat} from './styles'
import {useDropzone} from 'react-dropzone'

const Player = ({location}) => {

    const localSocketEndPoint = "http://127.0.0.1:8000";
    const globalSocketEndPoint = "https://socket-stremio-6h5gx42wjq-uc.a.run.app";
    //const globalSocketEndPoint = "http://127.0.0.1:8080";

    const [useGlobalSocket, setUseGlobalSocket] = useState(null)
    const [roomName, setRoomName] = useState('')
    const [userName, setUserName] = useState('')
    const [myUserId, setMyUserId] = useState(null)
    const [roomObj, setRoomObj] = useState(null)
    const [playing, setPlaying] = useState(false)

    const formatBytes = (a, b = 2) => { if (0 === a) return "0 Bytes"; const c = 0 > b ? 0 : b, d = Math.floor(Math.log(a) / Math.log(1024)); return parseFloat((a / Math.pow(1024, d)).toFixed(c)) + " " + ["Bytes", "KB", "MB", "GB", "TB", "PB", "EB", "ZB", "YB"][d] }
    
    const playerReference = useRef(null)
    const [movieObject, setMovieObject] = useState(null)
    const [myTorrent, setMyTorrent] = useState(null)
    const [subtitles, setSubtiltes] = useState(null)
    const [movieUrl, setMovieUrl] = useState(null)
    const [subUrl, setSubUrl] = useState(null)
    const [movieDownloadInfo, setMovieDownloadInfo] = useState(null)
    const electron = window.require('electron');
    const ipcRenderer = electron.ipcRenderer;


    const ConnectRoom = () => {

        const socket = socketIOClient(globalSocketEndPoint);

        setUseGlobalSocket(socket)

        socket.emit('enter', roomName, userName, movieObject, myTorrent)

    }

    const ExitRoom = () => {
        useGlobalSocket.disconnect()
        setUseGlobalSocket(null)
        setMyUserId(null)
    }



    const onDrop = useCallback(acceptedFiles => {
        // Do something with the files
        console.log(acceptedFiles)
        let subFile = acceptedFiles[0]
        console.log("new File")
        ipcRenderer.invoke('selectMySub', subFile.path)

    }, [])

    const {getRootProps} = useDropzone({onDrop})

    useEffect(() => {

        const localSocket = socketIOClient(localSocketEndPoint);

        vtt.setFontColor('white');
        vtt.setFontSize(40);

        return function cleanup() {
            localSocket.disconnect();
        }

    }, []);


    useEffect(() => {
        if(userName && location.actualMovie.directConnect){
            ConnectRoom()
        }
    }, [userName])

    useEffect(() => {
        if(location.actualMovie){
            console.log(location)

            setMovieObject(location.actualMovie.movie)
            setMyTorrent(location.actualMovie.torrent)

            if(location.actualMovie.directConnect){
                console.log(location.actualMovie.room.name)
                setRoomName(location.actualMovie.room.name)
                setUserName(location.actualMovie.directConnect)
            }
        }

    }, [location])


    useEffect(() => {
        if(myTorrent && movieObject){
            ipcRenderer.invoke('StartTorrent', {
                movieObject: movieObject,
                myTorrent: myTorrent,
            })
        }
    }, [myTorrent, movieObject])


    
    ipcRenderer.on('donwloadMovieUpdate', (evt, movieDownloadInfo) => {
        if(movieDownloadInfo.totalSize >= 1000000){
            setMovieUrl(`http://localhost:8000/movie/${movieDownloadInfo.movieUrl}`)
            setMovieDownloadInfo(movieDownloadInfo)
        }
    })

    ipcRenderer.on('setSubtiltes', (evt, subsObj) => {
        setSubtiltes(subsObj)
    })



    const reloadPlayer = () => {
        let actualTimePlayer = playerReference.current.getCurrentTime()
        setMovieUrl(null)
        setTimeout(() => {
            setMovieUrl(`http://localhost:8000/movie/${movieDownloadInfo.movieUrl}`)
            playerReference.current.seekTo(actualTimePlayer)
        }, 2000);
    }

    ipcRenderer.on('setSubUrl', (evt, subtitleAndress) => {
        setSubUrl(`http://localhost:8000/sub/${subtitleAndress}`)
    })



    useEffect(() => {
        if (useGlobalSocket) {


            useGlobalSocket.on('userCreate', function ({ userId }) {
                console.log('userCreate')
                console.log(userId)
                setMyUserId(userId)
            })

            useGlobalSocket.on('roomCreatedEvent', function (roomObj) {
                console.log('roomCreated')
                setRoomObj(roomObj)
                console.log(roomObj)
            })

            useGlobalSocket.on('enterRoom', function ({ movie }) {
                console.log('enterRoom')
                console.log(movie)
                setMovieObject(movie)
            })

            useGlobalSocket.on("pause", () => {
                setPlaying(false)
                
            })

            useGlobalSocket.on("play", (time) => {
                setPlaying(true)
                playerReference.current.seekTo(time)

            })

            return function cleanup() {
                useGlobalSocket.disconnect();
            }

        }
    }, [useGlobalSocket])




    return(
        <>
        <HeadBar/>
        <Container>
            <TopDiv>
                <input placeholder="Room Name" value={roomName} onChange={(evt) => setRoomName(evt.target.value)}/>
                <input placeholder="User Name" value={userName} onChange={(evt) => setUserName(evt.target.value)}/>
                {
                    !myUserId &&
                    <button onClick={ConnectRoom}>Create Room or Enter Room</button>
                }
                {
                    myUserId &&
                    <button onClick={ExitRoom}>Exit Rom</button>
                }
                {
                    movieObject &&
                    <MovieTitle>{movieObject.title}</MovieTitle>
                }
                {
                    movieDownloadInfo &&
                    <>
                        <p>
                            Baixado : {formatBytes(movieDownloadInfo.percentDownload)}
                            De : {formatBytes(movieDownloadInfo.totalSize)}
                        </p>
                        <p>
                            DownloadSpeed: {formatBytes(movieDownloadInfo.downloadSpeed)}
                        </p>
                    </>
                }

            </TopDiv>
            <CenterDiv  {...getRootProps()}>
                <PlayerAndChat>
                    <PlayerMovie roomObj={roomObj} movieUrl={movieUrl} setSubUrl={setSubUrl} subUrl={subUrl} subtitles={subtitles} reloadPlayer={reloadPlayer} playerReference={playerReference} useGlobalSocket={useGlobalSocket} setPlaying={setPlaying} playing={playing}/>
                    {
                        useGlobalSocket &&
                        <Chat userName={userName} useGlobalSocket={useGlobalSocket} roomName={roomName}/>

                    }
                </PlayerAndChat>




            </CenterDiv>
        </Container>
        </>

    )
}

export default Player