import { useEffect, useState } from 'react'
import {AlingFilters, Container, LeftBar, SearchValue, SelectCategory} from './styles'
import axios from 'axios';
import {Link } from 'react-router-dom'
import HeadBar from '../../Components/HeadBar/headBar';
import SideBar from '../../Components/SideBar/sideBar';
import ListRotate from '../../Components/ListRotate/ListRotate';
import ListFlat from '../../Components/ListFlatMovies/ListFlat';
import { WrapList } from '../../Components/WrapListMovie/WrapList';
import { Circles  } from 'react-loading-icons' 

function MainPage () {
    
    const [moviesList, setMoviesList] = useState(null)
    const [searchValue, setSearchValue] = useState("")
    const [filterCategoryValue, setFilterCategoryValue] = useState("")
    const [filterSortBy, setFilterSortBy] = useState("")
    const [loading, SetLoading] = useState(false)

    useEffect(() => {
        if(moviesList){
            console.log(moviesList)
        }
    }, [moviesList])

    useEffect(() => {
        setMoviesList(null)
        if(filterCategoryValue){
            setSearchValue(filterCategoryValue)
            SetLoading(true)
            axios.get(`https://yts.mx/api/v2/list_movies.json?genre=${filterCategoryValue}&with_rt_ratings=true&limit=30&sort_by=${filterSortBy}`)
            .then((response) => {
                SetLoading(false)
                setMoviesList(response.data.data.movies)
            })
            .catch((error) => {
                console.error(error);
            })
        }
    }, [filterCategoryValue, filterSortBy])
    
    return(
        <>
            <HeadBar setMoviesList={setMoviesList} setSearchValue={setSearchValue} SetLoading={SetLoading}></HeadBar>
            <Container>
                <AlingFilters>
                    <div>
                        <h3>Select Type Sort</h3>
                        <SelectCategory onChange={e => (setFilterSortBy(e.target.value))} style={{
                            width: '10vw',
                        }}>
                            <option value="title">title</option>
                            <option value="year">year</option>
                            <option selected value="rating">rating</option>
                            <option value="peers">peers</option>
                            <option value="seeds">seeds</option>
                            <option value="download_count">download_count</option>
                            <option value="like_count">like_count</option>
                            <option value="date_added">date_added</option>
                        </SelectCategory>
                    </div>
                    <div>

                        <h3>Select Movie Category</h3>
                        <SelectCategory  onChange={e => (setFilterCategoryValue(e.target.value))}>
                            <option value="comedy">comedy</option>
                            <option value="horror">horror</option>
                            <option value="animation">animation</option>
                            <option value="action">action</option>
                            <option value="romance">romance</option>
                            <option value="sci-fi">sci-fi</option>
                            <option value="mystery">mystery</option>
                            <option value="drama">drama</option>
                            <option value="crime">crime</option>
                            <option value="adventure">adventure</option>
                            <option value="fantasy">fantasy</option>
                        </SelectCategory>
                    </div>
                </AlingFilters>
            {
                searchValue?
                    <>  
                        <SearchValue>{searchValue}</SearchValue>
                        <WrapList movieList={moviesList}/>
                    </>
                    :
                    <>
                        <ListFlat filterSortBy={filterSortBy} typeToList='comedy'></ListFlat>
                        <ListFlat filterSortBy={filterSortBy} typeToList='horror'></ListFlat>
                        <ListRotate filterSortBy={filterSortBy} genery='animation'></ListRotate>
                        <ListRotate filterSortBy={filterSortBy} genery='fantasy'></ListRotate>
                        <ListFlat filterSortBy={filterSortBy} typeToList='mystery'></ListFlat>
                    </>
            }
            {
                loading &&
                <Circles  stroke="#000000"/>
            }
            </Container>
        </>
        )
    }
    
    export default MainPage