import {Container} from './styles'
import MovieCartaz from '../MovieCartaz/MovieCartaz'
export const WrapList = ({movieList}) => {
    console.log(movieList)
    return(
        <Container>
            {
                movieList &&
                    movieList.map((item) => (
                         
                        <MovieCartaz key={item.id} movie={item}>
                    
                        </MovieCartaz> 
                    
                    ))
            }  
           

        </Container>
    )
}