import { useEffect, useState } from 'react'
import HeadBar from '../../Components/HeadBar/headBar'
import {Container, Genere, GenereDiv, IconAndTextDiv, Icons, IconsDiv, ImageBackground, InfosDiv, LeftInfosDiv, LoadingIcon, MovieTitle, RoomName, Sinopsia, TextIcon} from './styles'
import FavIcon from '../../Icons/FavIcon.svg'
import TrailerIcon from '../../Icons/TrailerIcon.svg'
import axios from 'axios'
import ListTorent from '../../Components/ListTorrent/ListTorrent'

function SelectTorrent({ location }){

    const [movie, setMovie] = useState(null)
    const [loading, setLoading] = useState(true)

    useEffect(() => {
        if(location.actualMovie){
            axios.get(`https://yts.mx/api/v2/movie_details.json?movie_id=${location.actualMovie.id}&with_images=true&with_cast=true`)
            .then((response) => {
                console.log(response.data.data.movie)
                setLoading(false)
                setMovie(response.data.data.movie)
            })
            .catch((error) => {
                console.error(error);
            })

            console.log(location.actualMovie)
        }
    }, [location])
    
    return(
        <>

        <Container>
            <HeadBar></HeadBar>
        {
            loading &&
            <LoadingIcon/>
            
        }
        {

            movie&&

            <>
                <LeftInfosDiv>                
                        <MovieTitle>{movie.title}</MovieTitle>
                        <Sinopsia>{movie.description_intro.substring(0, 800) + "..."}</Sinopsia>
                        <GenereDiv>

                            {
                                movie.genres.map(genre => (
                                
                                    <Genere key={genre}>{genre}</Genere>
                                ))
                                
                            }
                        </GenereDiv>

                    
                        <IconsDiv>

                            <IconAndTextDiv>
                                <Icons src={FavIcon}></Icons>
                                <TextIcon>ADICIONAR A MINHA LISTA</TextIcon>
                            </IconAndTextDiv>

                            <IconAndTextDiv>

                                <Icons src={TrailerIcon}></Icons>
                                <TextIcon>ASSISTIR AO TRAILER</TextIcon>

                            </IconAndTextDiv>
                                                
                        </IconsDiv>


                </LeftInfosDiv>
                <ImageBackground url={movie.large_screenshot_image2}/>
                <ListTorent movie={movie}></ListTorent>


        </>
        }
        </Container>
        </>
    )
}

export default SelectTorrent;

