
export const goToLocaleProps = (locale,props, history) => {

    history.push({
        pathname: '/' + locale,
        actualMovie: props
    });

}
export const goToLocale = (locale, history) => {

    history.push('/' + locale);

}

export const goToGenery = (genery, history) => {

    history.push('/' + genery);

}

