import { goToLocaleProps } from '../../Functions/goTo'
import {Container,TorrentBox, Quality, Seeds, Title} from './styles'
import { useHistory } from "react-router-dom";

export const ListTorent = ({movie}) => {
    const torrents = movie.torrents
    const history = useHistory();

    return(

        <Container>
            {
                torrents&&
                torrents.map((item) => (
                    // 
                        <TorrentBox onClick={() => goToLocaleProps('player', {'movie': movie, 'torrent': item, 'directConnect' : null}, history)}>
                            <Title></Title>
                            <Seeds>seeds : {item.seeds}</Seeds>
                            <Quality>{item.quality}</Quality>
                        </TorrentBox>

                ))
            }
        </Container>
       

    )
}

export default ListTorent