import axios from 'axios'
import { useEffect, useState } from 'react'
import {Container, Carrousel, CarrouselButton, Title, LoadingIcon} from './styles'
import MovieCartaz from '../MovieCartaz/MovieCartaz'
import LeftCarrouselImg from '../../Icons/LeftCarrousel.svg'
import RigthCarrouselImg from '../../Icons/RigthCarrousel.svg'

const ListFlat = ({typeToList, filterSortBy}) => {

    const [moviesList, setMoviesList] = useState(null)
    const [positionX, setPositionX] = useState(0)
    const [loading, setLoading] = useState(true)
    const [mouseEnter, setMouseEnter] = useState(false)

    useEffect(() => {

        if(typeToList){

            axios.get(`https://yts.mx/api/v2/list_movies.json?genre=${typeToList}&with_rt_ratings=true&sort_by=${filterSortBy}`)
                .then((response) => {
                    setLoading(false)
                    setMoviesList(response.data.data.movies)
                })
                .catch((error) => {
                    console.error(error);
                })

        }
    }, [typeToList, filterSortBy])

    const SetPosition = (xValue) => {
        if(xValue > 0 && xValue <= 100){
            setPositionX(xValue)
        }
    }
    
    

    return(
        <>
            <Container>
                <Title>{typeToList}</Title>

                {
                    loading&&
                    <LoadingIcon></LoadingIcon>
                }

                <CarrouselButton url={RigthCarrouselImg} onClick={() => SetPosition(positionX+20)} ></CarrouselButton>

                <CarrouselButton url={LeftCarrouselImg} onClick={() => SetPosition(positionX-20)}></CarrouselButton>

                <Carrousel positionX={positionX}>
                {
                moviesList &&

                    moviesList.map((item) => (  
                        <MovieCartaz key={item.id} movie={item}>
                    
                        </MovieCartaz> 
                    
                    ))
                }  
                </Carrousel>
            </Container>
        </>
    )
}

export default ListFlat