import { useEffect, useState } from 'react'
import {Container, MessageInput, Messagens, Message, MessageBox, MessageContainer,ChatWindow, Timestamp,SmallSizeSpan} from './styles'


const Chat = ({userName, useGlobalSocket,roomName}) => {

    const [newMessage, setNewMessage] = useState(null)
    const [messagens, setMessagens] = useState([{user:'Room', value: 'Seja bem vindo', myMsg:false}])


    useEffect(() => {
        useGlobalSocket.on('chat-message', function (message) {
            let tempMessagens = messagens
            tempMessagens.push(message)
            setMessagens(tempMessagens)
        })

        useGlobalSocket.on('event-message', function ({ message }) {
            console.log('event-message')
            console.log(message);
            SendChatMessage(message)
        });

    }, [useGlobalSocket])

    const SendChatMessage = (message) => {
        let tempMessagens = messagens
        tempMessagens.push({
            user:roomName, value: message, myMsg:false
        })
    }


    const SendMessage = () => {
        if(newMessage != ""){
            let tempMessagens = messagens
            tempMessagens.push({
                user:userName, value: newMessage, myMsg:true
            })
            SendToSocket()
            setMessagens(tempMessagens)
            setNewMessage('')
        }
    }

    const SendToSocket = () => {
        useGlobalSocket.emit('sendMessage', {
            user:userName, value:newMessage, myMsg:false
        }, roomName)
    }



    return(
        <Container>
            <ChatWindow>
            {
                messagens.map((msg, i) => (
                    <MessageContainer  key={i}>
                        <MessageBox myMsg={msg.myMsg}>
                            <Messagens>
                                <Message > {msg.value} </Message>

                            </Messagens>
                            <Timestamp>
                                <SmallSizeSpan>
                                    {msg.user} 
                                </SmallSizeSpan>
                                <SmallSizeSpan>
                                    00:00
                                </SmallSizeSpan>
                            </Timestamp>
                        </MessageBox>
                    </MessageContainer>

                ))
            }
            </ChatWindow>
            <MessageInput placeholder="Esse filme Ã© uma maravilha" value={newMessage} onChange={(evt) => setNewMessage(evt.target.value)} onKeyDown={(evt) => evt.key === 'Enter' && SendMessage()}/>
        </Container>
    )
}
    
export default Chat  