import styled from "styled-components"

export const Container = styled.div`
    width: 100%;
    color:white;
    display:flex;
    flex-direction: column;
    align-items: center;
    
`

export const RoomContainer = styled.div`
    display:flex;
    width: 50%;
    margin-top:1rem;
    flex-direction: column;
`

export const AlignImageAndDetailsRoom = styled.div`
    display:flex;
    flex-direction: row;
`   

export const AlignDetailsColum = styled.div`
    display:flex;
    flex-direction: column;
    width:80%;
    justify-self: center;
    position: relative;
`

export const RoomMovieImage = styled.div`
    width: 12rem;
    height: 12rem;
    background-image: ${props =>` url(${props.url})`};
    background-size: cover;
    margin-top:.3rem;
    
`

export const RoomMovieName = styled.h3`
    
    border-bottom: 1px solid white;

`

export const RoomName = styled.h1`
    justify-self: center;
`

export const UserCont = styled.h3`

`

export const Entrar = styled.button`
    width:15rem;
    align-self: center;
    position: absolute;
    bottom: 0;
    border-radius:10px;
    padding:0.5rem;
    background-color:red;
    border:0px;
    transition: all 0.8s;
    :hover{
        cursor: pointer;
        color:white;
        padding:1rem;
    }

`
