import { SearchBox , SearchButton, SearchInput} from './styles'
import SearchIconImg from '../../Icons/search.svg'
import React, { useEffect, useState } from 'react'
import axios from 'axios'
import { goToLocale } from '../../Functions/goTo'
import { useHistory } from 'react-router'

import { faSearch } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

const Search = ({setMoviesList, setSearchValue, SetLoading}) =>{

    const [search, setSearch] = useState("")
    const history = useHistory();
    let delayToSearch
    useEffect(() => {
        setSearchValue(search.replaceAll("%20", " "))
        if(search == ""){
            setMoviesList(null)
            goToLocale('', history)
        }else{
            SetLoading(true)

        }
    }, [search])


    const SearchMovie = () => {
        console.log("callHere")
        SetLoading(true)
        clearTimeout(delayToSearch)
        axios.get('https://yts.mx/api/v2/list_movies.json?query_term=' + search)
                
        .then((response) => {
            setSearchValue(search.replaceAll("%20", " "))
            SetLoading(false)
            setMoviesList(response.data.data.movies)
        })

        .catch((error) => {

            console.error(error);
        })
    }

//     <div class="search-box">
//     <button class="btn-search"><i class="fas fa-search"></i></button>
//     <input type="text" class="input-search" placeholder="Type to Search...">
//   </div>



    return(

        // <SearchContainer haveValue={search}>
        //   <SearchInput haveValue={search} placeholder="Insira o filme" onChange={(evt) => setSearch(evt.target.value.replaceAll(" ", "%20"))}/>
        //   <SearchBtn>
        //     <SearchIcon src={SearchIconImg}/>
        //   </SearchBtn>
        // </SearchContainer>
            <SearchBox>
                <SearchButton><FontAwesomeIcon icon={faSearch} /></SearchButton>
                <SearchInput onKeyDown={(evt) => evt.key === 'Enter' && SearchMovie()} onChange={(evt) => setSearch(evt.target.value.replaceAll(" ","%20"))} />
            </SearchBox>
        
    )

}

export default Search